
import {
	mapMutations
} from 'vuex';
export default {
	methods: {
		...mapMutations(['setOpenId', 'login']),


	},
	onLaunch: function() {
		let that = this;
		uni.getStorage({
			key: 'token',
			success: res => {
				this.login(res.data); 
			},
			fail: err => {
				uni.navigateTo({
					url: "/pages/login/login"
				});
			}
		});


	},
	onShow: function() {
		console.log('App Show');
	},
	onHide: function() {
		console.log('App Hide');
	}
}
